
body {
  min-height: 100vh;
  margin: 0;
  background-color: #1d2120;
  overflow: hidden;
}
#app {
  font-family: 'Open Sans', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  text-align: center;
  color: #dfeae9;
}

.mh-calc {
  max-width: 1400px;
  margin: auto;
}
h1 {
  color: #e6d027;
}
.version-number {
  font-weight: 400;
  font-size: 12px;
  color: #777777;
  margin-top: 10px;
}
.main-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 40px;
  margin-bottom: 50px;
}
.bordered-box {
  border: 3px solid #c78306;
  border-radius: 5px;
  padding: 24px 32px;
  margin: 0 20px;
}
.input-item {
  padding: 10px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.input-label {
  font-weight: 600;
  margin-right: 40px;
}
.input-field {
  padding: 4px 5px;
  border-radius: 2px;
  border: 0;
  width: 80px;
}
a {
  color: #bbbbbb;
  text-decoration: none;
}
a:hover {
  color: #efefef;
}

.affinity-input {
  margin-right: 5px;
  width: 62px;
}

.damage-display {
  max-width: 400px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-bottom: auto;
}
.damage-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 12px;
}
.damage-detail {
  color: #777777;
}
.damage-final {
  font-size: 24px;
  font-weight: 600;
  margin-top: 30px;
  margin-bottom: 0;
}
.damage-final .damage-number {
  color: #62ff00;
}

.v-select .dropdown-toggle {
  border-color: white !important;
  background-color: white !important;
  width: 400px;
}
.skills-input {
  width: 580px;
}

.skills-display {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-right: -20px;
}

.skill-display-item {
  width: 406px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 16px 24px;
  margin: 0;
  margin-right: 30px;
  margin-top: 24px;
  position: relative;
}
.remove-link {
  position: absolute;
  top: 12px;
  right: 12px;

  display: inline-block;
  height: 20px;
  width: 20px;
  border: 1px solid #bbbbbb;
  border-radius: 50%;
}
.remove-link:hover {
  border-color: #efefef;
}
.skill-name {
  font-weight: 600;
  color: #24d0f7;
  font-size: 20px;
  text-align: left;
  margin-bottom: 12px;
}
.skill-edit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.skill-edit .input-label {
  margin-right: 20px;
}
.skill-edit .input-field {
  width: 50px;
}
.activation-input {
  margin-right: 5px;
}
